<template>
  <div class="page" v-nav="{hideNavbar: true}">
    <!-- 页头 -->
    <div class="page-content rebate-dashboard">
      <div class="rebate-header">
        <div class="nav clearfix">
          <div class="go-back" @click="back()"><i class="go-back-icon"></i></div>
          <div class="my-order" @click="onClick('orderlist/daifan')">
            <i class="order-icon"></i>
            <span>我的订单</span>
          </div>
        </div>

        <div class="desc"><span>淘宝天猫拿返利，最高返</span><img src="//qianka.b0.upaiyun.com/images/de266d2be09c3277d342f2597cb320d3.png" alt=""></div>

        <div class="search-bar text-center">
          <input type="search" placeholder="搜索宝贝／粘贴宝贝标题"  v-model="goodsName"><div @click="jumpToTaobao()">拿返利</div>
          <i class="search-icon" v-if="showGlass"></i>
        </div>

        <div class="bubble">
          <div v-for="(item, index) in keyWords" :key="item.key_id" @click="jumpWithKey(item.key_id, item.key_word)">
            <span v-text="item.key_word"></span>
          </div>
        </div>
      </div>
      <div class="rebate-help">
        <div class="help-header">
          <div class="line"></div>
          <span>如何购物拿返利</span>
          <div class="line"></div>
        </div>
        <div class="process text-center">
          <img src="//qianka.b0.upaiyun.com/images/ace785399c4a8c7da1ee1098249652df.png" alt="">
        </div>
      </div>

      <div class="action">
        <div @click="onClick('guide')"><span>图文教程</span><img src="//qianka.b0.upaiyun.com/images/2e009f78da4710572c5f6294726b73c0.png"></div>
        <div @click="onClick('help')"><span>常见问题</span><img src="//qianka.b0.upaiyun.com/images/f314ecace76ee8968a807ebe5abc2b6e.png"></div>
      </div>

    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import '../../scss/mixins';
  @import "./popups.scss";

  .rebate-dashboard {
    background: #FFFFFF;

    .rebate-header {
      height: 309px;
      background-image: linear-gradient(135deg, #FFA70F 0%, #F26327 100%);
      padding-top: 9px;
      margin-bottom: 40px;

      .nav {
        margin-bottom: 30px;

        .go-back {
          float: left;
          margin-left: 9px;
          width: 38px;
          height: 34px;
          background: rgba(233, 73, 0, 0.2);
          border-radius: 100px;
          display: inline-block;

          .go-back-icon::before {
            content: '';
            display: block;
            width: 10px;
            height: 15px;
            margin: 9px auto;
            background: url("//qianka.b0.upaiyun.com/images/fe6552fc07d98c196629e2cae1ec501f.png") no-repeat;
            background-size: 100% 100%;
          }
        }

        .my-order {
          float: right;
          margin-right: 8px;
          width: 100px;
          height: 34px;
          background: rgba(233, 73, 0, 0.2);
          border-radius: 100px;
          display: inline-block;
          padding: 0 14px;
          line-height: 34px;

          .order-icon {
            display: inline-block;
            width: 15px;
            height: 15px;
            content: '';
            background: url("//qianka.b0.upaiyun.com/images/baedfa1adfa6eb8fda74a2a769097b17.png") no-repeat;
            background-size: 100% 100%;
            vertical-align: middle;
            margin: -1px 0 0 -1px;
          }

          .order-icon-svg {
            display: inline-block;
            width: 15px;
            height: 15px;
            content: '';
            margin: 10px 4px 10px 14px;
            background: url("//qianka.b0.upaiyun.com/images/07f3b7755454c11517aa3b2d7d070a59.png") no-repeat;
            background-size: 100% 100%;
            vertical-align: middle;
          }

          span {
            line-height: 34px;
            height: 34px;
            font-size: 13px;
            color: #FFFFFF;
            vertical-align: middle;

            @include font-regular();
          }
        }
      }

      .desc {
        margin-bottom: 30px;
        margin-left: 31px;
        font-size: 24px;
        color: #FFFFFF;
        height: 33px;
        line-height: 30px;

        @include font-medium();

        span {
          vertical-align: middle;
        }

        img {
          margin-left: 3px;
          height: 22px;
          vertical-align: middle;
        }
      }

      .search-bar {
        position: relative;
        margin-bottom: 20px;

        input:first-child {
          padding: 0 0 0 31px;
          display: inline-block;
          width: 232px;
          height: 42px;
          vertical-align: middle;
          -webkit-appearance: none;
          border-radius: 2px;
          padding-top: 2px;

          @include font-regular();

          &::-webkit-input-placeholder {
            font-size: 13px;
            color: #C5C4C3;

            @include font-regular();
          }
        }

        .search-icon {
          position: absolute;
          left: 30px;
          top: 16px;
          display: inline-block;
          width: 12px;
          height: 12px;
          background: url("//qianka.b0.upaiyun.com/images/6787172633bd923678c0bdab9678ac75.png") no-repeat;
          background-size: 12px;
        }

        div:first-of-type {
          display: inline-block;
          margin-left: -1px;
          background-image: linear-gradient(48deg, #FFC33C 0%, #FE9206 100%);
          border-top-right-radius: 2px;
          border-bottom-right-radius: 2px;
          width: 80px;
          height: 44px;
          font-size: 14px;
          color: #FFF;
          line-height: 44px;
          vertical-align: middle;

          @include font-medium();
        }
      }

      .bubble {
        margin-left: 16px;
        font-size: 0;
        width: 353px;
        height: 68px;
        overflow: hidden;

        div {
          display: inline-block;
          margin-right: 10px;
          padding: 0 10px;
          height: 30px;
          background: rgba(255, 255, 255, 0.14);
          border-radius: 2px;
          margin-bottom: 8px;

          span {
            line-height: 30px;
            height: 30px;
            font-size: 11px;
            color: #FFFFFF;

            @include font-regular();
          }
        }
      }
    }

    .rebate-help {
      .help-header {
        margin-left: 16px;
        font-size: 0;
        margin-bottom: 20px;
        height: 18px;
        line-height: 18px;

        .line {
          display: inline-block;
          width: 101px;
          border-top: 0.5px solid #DDDDDD;
          vertical-align: middle;
        }

        span {
          margin-left: 25px;
          margin-right: 25px;
          font-size: 13px;
          color: #666666;
          letter-spacing: 0;
          vertical-align: middle;

          @include font-regular();
        }
      }

      .process {
        margin-bottom: 40px;
        height: 74px;

        img {
          height: 74px;
        }
      }
    }

    .action {
      font-size: 0;
      margin-bottom: 30px;
      height: 18px;
      line-height: 18px;

      div {
        display: inline-block;

        span {
          height: 18px;
          line-height: 18px;
          font-size: 13px;
          color: #3A3A3A;
          letter-spacing: 0;
          vertical-align: middle;

          @include font-regular();
        }

        img {
          width: 4px;
          height: 6px;
          margin-left: 5px;
          vertical-align: middle;
        }
      }

      div:first-child {
        margin-left: 111px;

        span {
          font-size: 13px;
          color: #FB800C;
          letter-spacing: 0;

          @include font-regular();
        }
      }

      div:nth-child(2) {
        margin-left: 30px;
      }

      div:nth-child(3) {
        margin-left: 20px;
      }
    }
  }
</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import {rebate_pause} from './popups'

  export default {
    components: {
      AnimateHeader
    },

    data () {
      return {
        showGlass: true,
        goodsName: '',
        keyWords: []
      }
    },

    mounted () {
      if (this.$route.name === 'fanli') return
      $api.rebateDashboard()
        .then((res) => res.data && res.data.payload)
        .then((payload) => {
          this.keyWords = payload.key_words
        })
    },

    methods: {
      back () {
        let fromShoutuPortal = this.$route.query.from_shoutu_portal
        if (fromShoutuPortal === 1) {
          $router.back('/tasks')
        } else {
          $router.back('/dashboard')
        }
      },

      onClick (path) {
        $router.forward('/rebate/' + path)
      },

      jumpToTaobao () {
        let popup = $popup.fromTemplate(rebate_pause.template(), rebate_pause.options)
        popup.show()
        if (popup) {
          return
        }
        let goodsName = this.goodsName.replace(/\s|\//g, '')
        if (!goodsName) {
          $toast.show('请输入宝贝名称')
          return
        }

        if (this.$route.name === 'fanli') {
          window.open('/v4/fanli/loading/' + goodsName)
        } else {
          window.open('/v4/rebate/loading/' + goodsName)
        }
      },

      jumpWithKey (key, word) {
        let popup = $popup.fromTemplate(rebate_pause.template(), rebate_pause.options)
        popup.show()
        if (popup) {
          return
        }
        window.open('/v4/rebate/loading/' + word + '/' + key)
      }
    }
  }
</script>



